// 640px, 1024px , 1400px

$breakpoints-up: (
  "medium": "40em",
  "large": "69.375em",
  "xlarge": "87.5em",
);

// 639px, 1023px , 1399px

$breakpoints-down: (
  "medium": "39.9375em",
  "large": "69.3125",
  "xlarge": "87.4375em",
);

@mixin breakpoint($size) {
  @media (min-width: map-get($breakpoints-up, $size)) {
    @content;
  }
}

@mixin breakpoint-down($size) {
  @media (max-width: map-get($breakpoints-down, $size)) {
    @content;
  }
}
